* {
  box-sizing: border-box;
}

html {
  font-size: 20px;
  font-family: serif;
  color: tan;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

figure {
  margin: 0;
}

figcaption {
  font-size: small;
  font-style: italic;
  text-align: right;
}
